<template>
  <div class="home position-relative">
    <HeaderNavbar></HeaderNavbar>
    <NavBar class="col-2 p-0 position-fixed top-0 start-0" v-if="isMobile"></NavBar>
    <main>
      <router-view/>
    </main>
  </div>
</template>

<style>
a {
  text-decoration: none;
  color: white;
}
.col-2 {
  width: 240px;
  z-index: 101;
}
@media screen and (min-width: 1248px) {
  .home {
    padding-left: 240px;
  }
}
</style>

<script>
// @ is an alias to /src
import NavBar from '@/components/NavBar.vue'
import HeaderNavbar from '@/components/HeaderNavbar.vue'
export default {
  name: 'HomeView',
  data () {
    return {
      isMobile: false
    }
  },
  components: {
    NavBar,
    HeaderNavbar
  },
  methods: {
    handleResize () {
      const windowWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth
      if (windowWidth > 1247) {
        this.isMobile = true
      } else {
        this.isMobile = false
      }
    }
  },
  created () {
    this.handleResize()
  }
}
</script>
