<template>
  <header class="top">
    <div class="container-fluid">
      <div class="header-top-wrapper">
        <div class="header-top-bar-wrapper" v-if="isDesktop">
          <div class="bar-controller">
            <a @click.prevent="openModal">
              <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36" style="fill: none; stroke: #fff; stroke-linecap: round; stroke-miterlimit: 10; stroke-width: 2px;">
                <line x1="5" y1="9" x2="31" y2="9"/>
                <line x1="5" y1="18.5" x2="31" y2="18.5"/>
                <line x1="5" y1="28" x2="31" y2="28"/>
              </svg>
            </a>
          </div>
        </div>
        <a href="#" v-if="isDesktop">
          <img src="../assets/casino-the-world-logo.png" alt="CASINO THE WORLD" class="header-top-logo">
        </a>
        <div class="header-top-auth">
          <a href="#" class="header-top-btn sign-in">Sign in</a>
          <a href="" class="header-top-btn sign-up">Sign up</a>
        </div>
        <div class="header-top-locales">
          <div class="select-lang-icons-btn">
            <div class="icons-btn-inner">
              <a href="#">
                <div class="code-icon">
                  <img src="https://cdn2.softswiss.net/i/flags/rectangular/en.svg" alt="en" class="flag-icon">
                </div>
                <div class="code-code">En</div>
              </a>
            </div>
          </div>
          <div class="select-lang-icons-btn-caret">
            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="12" fill="currentColor" class="bi bi-chevron-down" viewBox="0 0 16 16" preserveAspectRatio="xMidYMid slice">
              <path stroke="white" stroke-width="1.5" fill-rule="evenodd" d="M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z"/>
            </svg>
          </div>
        </div>
      </div>
    </div>
  </header>
  <BarModal ref="barModal"></BarModal>
</template>

<style>
header.top {
  width: 100%;
  padding: 26px 0 20px;
  background: linear-gradient(180deg,rgba(0,12,30,.7) 21.14%,rgba(0,12,30,0));
  display: flex;
  height: 160px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 101;
}
.header-top-logo {
  width: 100%;
  max-width: 180px;
  margin-left: 32px;
  display: inline-flex;
  align-items: center;
  flex-wrap: nowrap;
}
.header-top-wrapper {
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: auto 1fr auto;
  height: 100%;
  pointer-events: all;
}
.header-top-bar-wrapper {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: auto;
}
.header-top-auth {
  display: flex;
  gap: 10px;
  grid-column: span 4;
  grid-row: 3;
  justify-content: center;
  margin-left: auto;
  width: 100%;
}
.header-top-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex: 1 50%;
  white-space: nowrap;
  font-size: 14px;
  margin: 0;
  padding: 0;
}
.sign-in {
  background: transparent none;
  border: none;
  box-shadow: none;
  font-weight: 900;
  line-height: 1;
  outline: none;
  text-shadow: 0 0 4px #000c1e;
  transition: color .3s ease;
}
.sign-in:hover {
  color: #ffb81e;
}
.sign-up {
  background: #ffb81e;
  border: none;
  border-radius: 40px;
  color: #000c1e;
  flex-wrap: wrap;
  font-weight: 800;
  line-height: 1.2;
  min-height: 40px;
  min-width: 100px;
  padding: 0 24px;
  position: relative;
  text-shadow: none;
  vertical-align: middle;
  transition: background-color .3s ease,color .3s ease,box-shadow .3s ease;
}
.sign-up:hover {
  background: #ffb81e;
  box-shadow: 0 0 40px rgba(255,184,30,.3);
}
.header-top-locales {
  display: inline-flex;
  align-items: center;
  align-self: stretch;
  justify-content: center;
  width: auto;
}
.select-lang-icons-btn {
  cursor: pointer;
  flex-shrink: 0;
  flex-wrap: nowrap;
}
.code-icon {
  display: inline-flex;
  vertical-align: top;
  width: 22px;
}
.flag-icon {
  display: inline-block;
  vertical-align: top;
  width: 100%;
}
.code-code {
  color: #ffffff;
  display: inline-block;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: .12px;
  margin: 0 0 0 5px;
  vertical-align: top;
  white-space: nowrap;
  transition: color .3s ease;
}
.select-lang-icons-btn-caret {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 0 5px;
  transition: transform .3s;
  width: auto;
}
@media screen and (min-width: 768px){
  header.top {
    height: 80px;
    padding: 0;
  }
  .container-fluid {
    padding: 0 104px;
  }
  .header-top-wrapper {
    display: flex;
    align-items: center;
  }
  .header-top-auth {
    gap: 16px;
    width: auto;
  }
  .header-top-btn {
    flex: 0;
    min-width: 112px;
  }
  header.top .header-top-locales {
    margin-left: 16px;
  }
}
@media screen and (min-width: 960px) {
  header.top .header-top-logo {
    margin: 0 auto 0 54px;
  }
  header.top .header-top-locales {
    margin-left: 32px;
  }
}
@media screen and (min-width: 1248px) {
  .container-fluid {
    padding: 0 32px;
  }
}
</style>

<script>
import BarModal from './BarModal.vue'
export default {
  data () {
    return {
      isDesktop: false
    }
  },
  components: {
    BarModal
  },
  methods: {
    openModal () {
      const barModal = this.$refs.barModal
      barModal.showModal()
    },
    handleResize () {
      const windowWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth
      if (windowWidth < 1248) {
        this.isDesktop = true
      } else {
        this.isDesktop = false
      }
    }
  },
  created () {
    this.handleResize()
  }
}
</script>
